.team4 {
  font-family: "Montserrat", sans-serif;
  color: #8d97ad;
  font-weight: 300;
  background-color: #3b4656;
}

.team4 h1,
.team4 h2,
.team4 h3,
.team4 h4,
.team4 h5,
.team4 h6 {
  color: #3e4555;
}

.team4 .font-weight-medium {
  font-weight: 500;
}

.team4 h5 {
  font-family: "Odor Mean Chey", Sans-serif;
  font-size: 1.1em;
  letter-spacing: 1px;
  color: white;
}

.team4 .subtitle {
  color: #f6a623;
  font-size: 12px;
  font-weight: bold;
  line-height: 1.6em;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  margin-top: 1em;
}
.team4 .profile_image_container {
  padding: 8px;
}
.team4 .profile_image {
  transition: 0.2s;
  cursor: pointer;
  max-width: 100%;
  border: 3px solid #ffffff;
}

.team4 .profile_image:hover {
  border: 3px solid #f6a623;
}
